











import Vue from "vue";
import dataPrivacy from './data-privacy.component.vue';
export default Vue.extend({
  components: {
    dataPrivacy
  },
  mounted() {
      const agreed = Boolean(localStorage.getItem('gdpr-agreed'));
      this.show = !agreed
  },
  data() {
    return {
      show: false
    };
  },
  methods: {
    onAgree() {
      this.show = false;
      localStorage.setItem('gdpr-agreed', "1")
    }
  }
});
